html, body {
  font-weight: 400;
  font-family: "Roboto", sans-serif;
  line-height: 1.5;
  color: #000000;
  -webkit-font-smoothing: antialiased;
}

h1, .h1.h1 {
  font-weight: 400;
  font-family: "DM Serif Display", serif;
  line-height: 1;
  font-size: 48px;
  color: #002855;
}
@media (min-width: 1024px) {
  h1, .h1.h1 {
    font-size: 72px;
  }
}

h2, .h2.h2 {
  font-weight: 400;
  font-family: "DM Serif Display", serif;
  line-height: 1;
  font-size: 36px;
  color: #002855;
}
@media (min-width: 1024px) {
  h2, .h2.h2 {
    font-size: 50px;
  }
}

h3, .h3.h3 {
  font-weight: 400;
  font-family: "Roboto Condensed", sans-serif;
  line-height: 1;
  font-size: 28px;
}
@media (min-width: 1024px) {
  h3, .h3.h3 {
    font-size: 38px;
  }
}

h4, .h4.h4 {
  font-weight: 400;
  font-family: "Roboto Condensed", sans-serif;
  line-height: 1;
  font-size: 23px;
}
@media (min-width: 1024px) {
  h4, .h4.h4 {
    font-size: 28px;
  }
}

h5, .h5.h5 {
  font-weight: 400;
  font-family: "Roboto Condensed", sans-serif;
  line-height: 1;
  font-size: 20px;
}
@media (min-width: 1024px) {
  h5, .h5.h5 {
    font-size: 21px;
  }
}

h6, .h6.h6 {
  font-weight: 400;
  font-family: "Roboto Condensed", sans-serif;
  line-height: 1;
  font-size: 16px;
}

p, .p1.p1 {
  font-size: 16px;
}
p + p, .p1.p1 + p {
  padding: 16px 0 0;
}
p b, p strong, .p1.p1 b, .p1.p1 strong {
  font-weight: 400;
  font-family: "Roboto", sans-serif;
  line-height: 1.5;
}

.p2.p2 {
  font-size: 14px;
}

.p3.p3 {
  font-size: 10px;
}

.e1.e1 {
  font-family: "Knewave", system-ui;
  font-weight: 400;
  font-style: normal;
  text-transform: uppercase;
  transform: rotate(-3deg);
  display: inline-block;
  line-height: 1;
  margin: 0 0 0.5em -0.25em;
  font-size: 32px;
}

.e2.e2 {
  font-family: "Knewave", system-ui;
  font-weight: 400;
  font-style: normal;
  text-transform: uppercase;
  transform: rotate(-3deg);
  display: inline-block;
  line-height: 1;
  margin: 0 0 0.5em -0.25em;
  font-size: 24px;
}

.e3.e3 {
  font-family: "Knewave", system-ui;
