a {
  color: #002855;
}

sup {
  font-size: 0.5em;
}

q, blockquote {
  font-weight: 400;
  font-family: "Roboto", sans-serif;
  line-height: 1.5;
  font-size: 18px;
  color: #000000;
  padding: 16px 0 32px;
}
@media (min-width: 1024px) {
  q, blockquote {
    font-size: 32px;
  }
}

b, strong {
  font-weight: bold;
}

pre {
  font-family: Consolas, "Andale Mono WT", "Andale Mono", "Lucida Console", "Lucida Sans Typewriter", "DejaVu Sans Mono", "Bitstream Vera Sans Mono", "Liberation Mono", "Nimbus Mono L", Monaco, "Courier New", Courier, monospace;
  padding: 8px;
  color: #FFFFFF;
  background: #000000;
  border: 2px solid #8E8C99;
  margin: 12px 0 32px;
  border-radius: 10px;
  white-space: pre-line;
}

hr {
  margin: 24px 0;
  background: #F3F3F5;
  height: 2px;
  width: 100%;
}

table tr {
  padding: 8px 0;
}
table th, table td {
  padding: 8px;
  border-bottom: 2px solid #0057A8;
}

.translucent {
  opacity: 0.5;
}

.ck-content {
  box-sizing: border-box;
}
.ck-content {
  background: #EBEBEB !important;
}
.ck-content h1, .ck-content h2, .ck-content h3,
.ck-content h4, .ck-content h5, .ck-content h6 {
  padding: 0 0 24px;
}